//------------------------------------------------------------------------------
// @setting: Colors
//------------------------------------------------------------------------------
// @description
//
// A lbrary of colors built upon the Google Material Design base using a more
// consistent and programtical approach.
//
//------------------------------------------------------------------------------

//
// @settings
@function get-color($color, $value: 0) {
	@return map-get($color, $value);
}
//
// grayscale
$gray-50:                      #fafafa !default;    // 98
$gray-100:                     #f5f5f5 !default;    // 96
$gray-200:                     #eeeeee !default;    // 93
$gray-300:                     #e0e0e0 !default;    // 88
$gray-400:                     #bdbdbd !default;    // 74
$gray-500:                     #9e9e9e !default;    // 62
$gray-600:                     #757575 !default;    // 46
$gray-700:                     #616161 !default;    // 38
$gray-800:                     #424242 !default;    // 26
$gray-900:                     #212121 !default;    // 12

$dark:                    		#121212 !default;
$dark-1:                		chromatic-color-brighten($dark, 5) !default;
$dark-2:                		chromatic-color-brighten($dark, 7) !default;
$dark-3:                		chromatic-color-brighten($dark, 8) !default;
$dark-4:                		chromatic-color-brighten($dark, 9) !default;
$dark-6:                		chromatic-color-brighten($dark, 11) !default;
$dark-8:                		chromatic-color-brighten($dark, 12) !default;
$dark-12:                		chromatic-color-brighten($dark, 14) !default;
$dark-16:                		chromatic-color-brighten($dark, 15) !default;
$dark-24:                		chromatic-color-brighten($dark, 16) !default;

$black:                        #000000 !default;    // 0
$black-87:                     $gray-900 !default;
$black-54:                     $gray-600 !default;
$black-38:                     $gray-500 !default;
$black-12:                     $gray-300 !default;
$white:                        #ffffff !default;    // 100

$grayscale: chromatic-scale(#fafafa, #212121, (stops: 10));

$grey-50: get-color($grayscale, 0) !default;
$grey-100: get-color($grayscale, 1) !default;
$grey-200: get-color($grayscale, 2) !default;
$grey-300: get-color($grayscale, 3) !default;
$grey-400: get-color($grayscale, 4) !default;
$grey-500: get-color($grayscale, 5) !default;
$grey-600: get-color($grayscale, 6) !default;
$grey-700: get-color($grayscale, 7) !default;
$grey-800: get-color($grayscale, 8) !default;
$grey-900: get-color($grayscale, 9) !default;

